<template>
  <div class="sell-control">
    <div class="block">
      <span class="demonstration">时间范围</span>
    <el-date-picker
      v-model="value1"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      value-format="yyyy-MM-dd">
    </el-date-picker>
      <el-button type="primary" @click="onSubmit">查询</el-button>
    </div>
    <div class="sell-control_echarst">
      <div id="main" style="width:100%;height:500px;"></div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import echarts from "echarts";

export default {
  data() {
    return {
     pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
    value1:"",
        
    };
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
     var myChart = echarts.init(document.getElementById("main"));
      this.$axios.get('http://127.0.0.1:3333/counter.php?a=goods&field=date').then(data=>{
        // console.log(data.data.data)
        let ctime=[];
       let num=[];
        data.data.data.forEach(item=>{
          // console.log(item.ctime)
          ctime.push(item.ctime)
          num.push(Number(item.num))
          
        })
        // console.log(ctime)
          myChart.setOption({
            title: {
              text: "商品统计"
            },
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross",
                label: {
                  backgroundColor: "#6a7985"
                }
              }
            },
          
            xAxis: {
            type: 'category',
            boundaryGap: false,
            data:ctime,
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                data:num,
                type: 'line',
                areaStyle: {}
            }]
          })
       
      })
    },
    onSubmit() {
     var myChart = echarts.init(document.getElementById("main"));
      this.$axios.get('http://127.0.0.1:3333/counter.php?a=goods&field=date').then(data=>{
        let num1=[];
        data.data.data.forEach(item=>{
          num1.push(Number(item.num))
        })
        // console.log(this.value1[0])
          myChart.setOption({
            title: {
              text: "商品统计"
            },
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross",
                label: {
                  backgroundColor: "#6a7985"
                }
              }
            },
          
            xAxis: {
            type: 'category',
            boundaryGap: false,
            data:this.value1,
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                data:num1,
                type: 'line',
                areaStyle: {}
            }]
          })
       
      })
    
    },
   
  }
};
</script>

<style >
.sell-control .block {
  text-align: left;
  margin-bottom: 44px;
}
.sell-control .block .el-date-editor {
  width: 550px;
}
.sell-control .block .demonstration {
  margin-right: 18px;
}
.sell-control .block .el-button {
  margin-left: 18px;
}
.sell-control_echarst {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}
.sell-control .sell-control_echarst {
  width: 100%;
  height: 60%;
  background: #ffffff;
}
.sell-control  #main>div>canvas{
  padding: 14px 0 0 14px !important;

}
</style>